.main-section {
  background-color: #012F34;
  grid-column: 1 / span 3;
  padding: 110px 10px 40px;
  color: $white;
  // height: 650px;

  @include for-medium-layout {
    padding: 0;
    height: 600px;
  }

  .main-container {
    grid-column: 2 / span 1;
    position: relative;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    // gap: 30px;

    @include for-medium-layout {
      flex-direction: row;
      align-items: center;
      height: 100%;
      gap: 140px;
    }

    .design-div {
      width: 60px;
      height: 5px;
      background-color: #f67e7e;
      position: absolute;
      top: -40px;
      
      @include for-medium-layout {
        top: 160px;
      }
    }

    h2 {
      max-width: 300px;
      font-size: 1.75rem;
      font-weight: bold;
      margin-bottom: 30px;
      
      @include for-medium-layout {
        margin-bottom: 0;
        margin-inline-end: 30px;
        max-width: 380px;
        font-size: 2.75rem;
      }
    }

    .secondary-container {
      display: flex;
      flex-direction: column;
      // gap: 10px;

      >*:not(:last-child) {
        margin-bottom: 10px;
      }

      .item-container {
        display: flex;
        flex-direction: column;
        align-items: center;
        text-align: center;
        // gap: 10px;
 
      >*:not(:last-child) {
        margin-bottom: 10px;

        @include for-medium-layout { 
          margin-bottom: 0;
          margin-inline-end: 10px;
        }
      }

        @include for-medium-layout {
          flex-direction: row;
          align-items: initial;
          text-align: left;
        }

        img {
          width: 70px;
          height: 70px;
        }

        .item-txt {
          display: flex;
          flex-direction: column;
          // gap: 20px;

          >*:not(:last-child) {
            margin-bottom: 15px;
          }
        }

        h4 {
          color: #f67e7e;
        }
      }
    }
  }
}